<template>
	<view class="order_wrap">
		<view class="role_wrap flex-lr flex-center">
			<view class="role_item" :class="index ==i?'on':''"  v-for="(desc,i) in list" :key='desc'  @click='change(i)'><text>{{desc}}</text></view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'order_wrap',
		props:['index'],
		data(){
			return{
				list:['发布中','待进行','进行中','已取消','已结束']
			}
		},
		methods:{
			change(i){
				this.$emit('change',i)
			}
		}
	}
</script>

<style lang="less" scoped>

	.order_wrap {
		.role_wrap {
			padding: 0 30rpx;
			height: 88rpx;
			background: #FFFFFF;
			.role_item {
				font-size: 28rpx;
				font-weight: 500;
				color: #848484;
				line-height: 36rpx;
				&.on {
					font-size: 36rpx;
					font-weight: bold;
					color: #1A1A1A;
					line-height: 36px;
					text {
						position: relative;
						&:after {
							content: '';
							display: block;
							width: 109rpx;
							height: 28rpx;
							background: linear-gradient(0deg, rgba(#0493FF, 0.56),rgba(#0493FF, 0.1));
							position: absolute;
							bottom: 0;
							z-index: 6;
							left: 0;
							right: 0;
							margin: 0 auto;
						}

					}
				}
			}
		}
	}
</style>
